/* You can add global styles to this file, and also import other style files */

@import './font-oswald.less';

@fill: -webkit-fill-available;

html, body {
  width: @fill;
  height: @fill;
}
hr {
  border-width: 5px;
  opacity: 1;
}

li {
  margin-block: .25rem;
}
.li-num-2 {
  list-style: decimal-leading-zero;
}

.w-fit {
  width: -moz-fit-content;
  width: fit-content;
}

.hoverable:hover {
  background-color: hsla(0, 0%, 0%, .15);
}

.fit-cover {
  object-fit: cover;
}
.fit-contain {
  object-fit: contain;
}
.fit-fill {
  object-fit: fill;
}
.fit-down {
  object-fit: scale-down;
}
