html, body {
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
}

h1, h2, h3 {
  font-family: 'Oswald', Arial, sans-serif;
  font-weight: 400;
  margin-bottom: 0;

  h1 {
    font-size: 50px;
  }
  h2 {
    font-size: 38px;
  }
  h3 {
    font-size: 24px;
  }
}

small {
  font-family: Roboto, sans-serif;
  font-size: smaller;
}
